import React from 'react'
import ada from "../images/AlanTuring.jpg"
import frco from "../images/AdaLovelance.jpg"
import adri from "../images/LinusTorvalds.jpg"
import ander from "../images/SteveJobs.jpg"
import nata from "../images/GraceHooper.jpg"
import jeni from "../images/TimBerners.jpg"
import { Footer } from './Footer';

export const Pioneros = () => {
  return (
    <>
        <center>
            <div className="container" style={{marginTop:20}}>

            <div className="row">
                <div className="col-lg-6">
                    <img className="rounded-circle" width="140" height="140" src={ada} alt="..." />
                    <h2>Alan Turing</h2>
                    <p><b>Contribución:</b> Pionero en la teoría de la computación y en la creación de la máquina de Turing, un modelo fundamental para la computación moderna. <br/><b>Destacado por:</b> Su papel crucial en descifrar códigos encriptados durante la Segunda Guerra Mundial y sus contribuciones teóricas a la ciencia de la computación.</p>
                    <p><a className="btn btn-secondary" href="# ">Ver Más &raquo;</a></p>
                </div>
                <div className="col-lg-6">
                    <img className="rounded-circle" width="140" height="140" src={frco} alt="..."/>
                    <h2>Ada Lovelance</h2>
                    <p><b>Contribución:</b> Considerada la primera programadora, Ada Lovelace desarrolló algoritmos para la Máquina Analítica de Charles Babbage y escribió notas que incluían el primer algoritmo destinado a ser procesado por una máquina, estableciendo así las bases de la programación. <br/><b>Destacado por:</b> Su visión única sobre las aplicaciones potenciales de las máquinas analíticas, que iban más allá de simples cálculos matemáticos. </p>                    <p><a className="btn btn-secondary" href="# ">Ver Mas &raquo;</a></p>
                </div>
                <div className="col-lg-6">
                    <img className="rounded-circle" width="140" height="140" src={adri} alt="..."/>
                    <h2>Linus Torvalds</h2>
                    <p>
                        <b>Contribución:</b> Cofundador de Apple, líder en la introducción de productos innovadores como la Macintosh, iPod, iPhone y iPad.
                       <br/><b>Destacado por:</b> Su enfoque en el diseño estético, la simplicidad y la experiencia del usuario, así como su impacto en la industria de la tecnología y el entretenimiento.</p>
                    <p><a className="btn btn-secondary" href="# ">Ver Mas &raquo;</a></p>
                </div>
                <div className="col-lg-6">
                    <img className="rounded-circle" width="140" height="140" src={ander} alt="..."/>
                    <h2>Steve Jobs</h2>
                    <p>
                        <b>Contribución:</b> Creador y desarrollador principal del kernel de Linux, un sistema operativo de código abierto utilizado en servidores y dispositivos embebidos en todo el mundo.
                        <br/><b>Destacado por:</b> Su compromiso con el software de código abierto y su influencia en el desarrollo colaborativo de sistemas operativos.
                    </p>
                    <p><a className="btn btn-secondary" href="# ">Ver Mas &raquo;</a></p>
                </div>
                <div className="col-lg-6">
                    <img className="rounded-circle" width="140" height="140" src={nata} alt="..."/>
                    <h2>Grace Hopper</h2>
                    <p>
                    <b>Contribución: Pionera</b> en la programación de computadoras y desarrolladora del primer compilador, A-0. Contribuyó significativamente al desarrollo de COBOL, un lenguaje de programación de alto nivel.
                    <br/><b>Destacado por:</b> Su liderazgo en la estandarización de lenguajes de programación y su papel en la promoción de estándares de software.
                    </p>
                    <p><a className="btn btn-secondary" href="# ">View details &raquo;</a></p>
                </div>
                <div className="col-lg-6">
                    <img className="rounded-circle" width="140" height="140" src={jeni} alt="..."/>
                    <h2>Tim Berners-Lee</h2>
                    <p>
                    <b>Contribución:</b> Inventor del World Wide Web (WWW) y del protocolo HTTP. Desarrolló la primera página web y el primer navegador web.
                    <br/><b>Destacado por:</b> Su contribución fundamental para hacer que la información y la comunicación en la red fueran accesibles y utilizables para el público en general.
                    </p>
                    <p><a className="btn btn-secondary" href="# ">View details &raquo;</a></p>
                </div>
                </div>

            </div>
        </center>
        <Footer />
    </>
  )
}
