import React from 'react'
import card1 from '../images/john_m.jpg'
import card2 from '../images/dennis_r.jpg'
import card3 from '../images/vinton_c.jpg'

export const ContentRevolucionarios = () => {
  return (
    <div className="container">
        <div className="row row-cols-1 row-cols-md-3 g-4">
            <div className="col">
                <div className="card">
                <img src={card1} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">John McCarthy</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Científico de la computación y pionero en inteligencia artificial (IA). Desarrolló el lenguaje de programación Lisp y acuñó el término "inteligencia artificial".
                    <br/><b>Destacado por:</b> Su liderazgo en la investigación de la inteligencia artificial y su influencia en el desarrollo de lenguajes de programación.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card2} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Dennis Ritchie</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Creador del lenguaje de programación C y co-creador del sistema operativo UNIX. Contribuyó significativamente al desarrollo de la informática moderna.
                    <br/><b>Destacado por:</b> Su impacto en la programación de sistemas y su trabajo en el desarrollo de herramientas fundamentales para el software.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card3} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Vinton Cerf</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Ingeniero informático y uno de los "padres de Internet". Co-diseñó el protocolo de comunicaciones TCP/IP, la base de Internet.
                    <br /><b>Destacado por: </b>Su papel en la creación de los fundamentos de Internet y su trabajo en la promoción de estándares abiertos y la conectividad global.
                    </p>
                </div>
                </div>
            </div>
        </div>
    </div>
  )
}
