import React from 'react'
import card1 from '../images/elon_m.jpg'
import card2 from '../images/anne_m.jpg'
import card3 from '../images/ray_k.jpg'

export const ContentFuturos = () => {
  return (
    <div className="container">
        <div className="row row-cols-1 row-cols-md-3 g-4">
            <div className="col">
                <div className="card">
                <img src={card1} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Elon Musk</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Empresario y fundador de SpaceX y Tesla, entre otras compañías. Ha expresado interés y ha invertido en el desarrollo de tecnologías avanzadas, como la inteligencia artificial y la colonización de Marte.
                    <br/><b>Destacado por:</b> Su visión audaz en el avance de la tecnología, incluidas las iniciativas para llevar a los humanos a Marte y sus preocupaciones sobre los riesgos de la inteligencia artificial.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card2} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Anne-Marie Imafidon</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Matemática, científica informática y defensora de la educación en tecnología. Fundadora de Stemettes, una organización que promueve la participación de las mujeres en las disciplinas STEM.
                    <br/><b>Destacado por:</b> Su trabajo en la promoción de la diversidad y la inclusión en la tecnología y la inspiración de las futuras generaciones de mujeres en STEM.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card3} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Ray Kurzweil</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Inventor y futurista, conocido por sus predicciones sobre la convergencia de la tecnología y la inteligencia artificial, así como por su trabajo en el desarrollo de tecnologías de reconocimiento de voz y lectura óptica de caracteres.
                    <br /><b>Destacado por:</b> Su visión del futuro tecnológico y la idea de la "singularidad tecnológica", un punto en el futuro en el que la inteligencia artificial superará la inteligencia humana.
                    </p>
                </div>
                </div>
            </div>
        </div>
    </div>
  )
}
