import React from 'react'
import card1 from '../images/margaret_h.jpg'
import card2 from '../images/barbara_l.jpg'
import card3 from '../images/radia_p.jpg'

export const ContentMujeres = () => {
  return (
    <div className="container">
        <div className="row row-cols-1 row-cols-md-3 g-4">
            <div className="col">
                <div className="card">
                <img src={card1} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Margaret Hamilton</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Pionera en la ciencia de la computación y directora de la División de Ingeniería de Software del Laboratorio de Instrumentación del MIT. Desarrolló el software de vuelo para las misiones Apolo de la NASA.
                    <br/><b>Destacado por:</b> Su liderazgo en el diseño del software de vuelo crítico para el alunizaje y su contribución al establecimiento de la ingeniería de software como disciplina.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card2} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Barbara Liskov</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Informática y profesora, conocida por sus contribuciones a la programación orientada a objetos y la creación del principio de sustitución de Liskov.
                    <br/><b>Destacado por:</b> Ser la primera mujer en obtener un doctorado en ciencias de la computación y por su impacto en el diseño de lenguajes de programación.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card3} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Radia Perlman</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                     <b>Contribución:</b> Ingeniera de redes y programadora, conocida por sus contribuciones al desarrollo de los protocolos de red y por inventar el algoritmo Spanning Tree.
                    <br/><b>Destacado por:</b> Su papel fundamental en la arquitectura de redes y su enfoque en la resiliencia y la redundancia en las comunicaciones en red.
                    </p>
                </div>
                </div>
            </div>
        </div>
    </div>
  )
}
