import React from 'react'
import card1 from '../images/dj_p.jpg'
import card2 from '../images/fei_f.jpg'
import card3 from '../images/hadley_w.jpg'

export const ContentCienciaDatos = () => {
  return (
    <div className="container">
        <div className="row row-cols-1 row-cols-md-3 g-4">
            <div className="col">
                <div className="card">
                <img src={card1} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">DJ Patil</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Científico de datos y estadístico, conocido por acuñar el término "científico de datos" y por su trabajo en la aplicación de datos para mejorar la toma de decisiones en el gobierno y la industria.
                    <br /><b>Destacado por:</b> Su papel como el primer Científico de Datos de los Estados Unidos y su promoción del uso de datos para abordar problemas complejos.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card2} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Fei-Fei Li</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Investigadora en inteligencia artificial y visión por computadora, cofundadora de AI4ALL. Ha trabajado en el desarrollo de algoritmos de aprendizaje profundo y en la promoción de la diversidad en la inteligencia artificial.
                    <br /><b>Destacado por: </b>Su liderazgo en la investigación en visión por computadora y su compromiso con la inclusión y la diversidad en el campo.
                    </p>
                </div>
                </div>
            </div>
            <div className="col">
                <div className="card">
                <img src={card3} className="card-img-top" alt="..." height="250 px"/>
                <div className="card-body">
                    <h5 className="card-title">Hadley Wickham</h5>
                    <p className="card-text" style={{textAlign:"justify"}}>
                    <b>Contribución:</b> Estadístico y científico de datos, conocido por sus contribuciones al desarrollo de paquetes de software en R, como ggplot2 y dplyr, que son ampliamente utilizados en análisis de datos.
                    <br /><b>Destacado por:</b> Su influencia en la comunidad de la ciencia de datos y su trabajo en la creación de herramientas que facilitan la visualización y manipulación de datos en R.</p>
                </div>
                </div>
            </div>
        </div>
    </div>
  )
}
